/* @import '~leaflet/dist/leaflet.css'; */

@font-face {
  font-family: 'Atkinson';
  src: local('Atkinson') url(./fonts/AtkinsonHyperlegible-Regular.ttf) format('ttf');
}

.App {
  /* text-align: center; */
  background-color: lightgray;
  font-family: 'Atkinson';
  font-size: 1rem;
}

.bg-myBg{
  background-color: rgb(160, 160, 160);
  box-shadow: 5px 5px 5px rgb(107, 106, 106);
}

.bg-adminBg{
  background-color: black;
}

.containerWrapper {
  background-color: white;
  box-shadow: 5px 5px 5px rgb(107, 106, 106);
}

.content {
  margin-top: 30px;
  padding: 50px;
  
}

.leaflet-container {
  width: 100vw;
  height: 100vh;
}

.article {
  margin-top: 50px;
}

 .navbar {
  color: #800000;
  font-size: 1.1rem;
 
}

.categories {
  display: flex;
}

.pageWrapper{
  margin-top: 50px;
}

.item {
  color: #800000;
  margin-right: 20px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 18px;
  padding-right: 18px;
  border-radius: 30px;
  border: 1px solid rgb(155, 154, 154);
}

.item:hover {
  background-color: #800000;
  color: white;
  transform: scale(1);
  cursor: pointer;
  transition: all 0.5s;
}

.readOn {
  font-style: italic;
  text-align: center;
  color: #800000;;
}

.blogpost {
  position: relative;
  padding: 30px;
  text-align: justify;
}

.blogpostBorderTop {
  position: absolute;
  width: 15%;
  height: 30%;
  border-left:  2px solid #800000;
  border-top: 2px solid #800000;
  border-top-left-radius: 20%;
}

/* .blogpostBorderBtm {
  position: absolute;
  top: 83%;
  left: 80%;
  width: 15%;
  height: 10%;
  border-right:  2px solid #800000;
  border-bottom: 2px solid #800000;
  border-bottom-right-radius: 20%;
} */

.blogpostInner {
  
  margin: 30px;
}

.navstyle {
  text-decoration : 'none';
  color: '#800000';
}
.active {
  border-bottom: 1px solid #800000;
 }

.editorBlock{
  border: 1px solid lightgrey;
  padding: 10px;
}

.draft-editor-wrapper{
  border: 1px solid lightgrey;
  min-height: 150px;
}

.editBtn {
  background: white;
  border: none;
  border-right: 1px solid  #800000;
  box-shadow: 0px 2px 2px #800000;
  margin-right: 2px;
  margin-bottom: 10px;
}



.activeEdit{
  background: #800000;
  color: white;
}

.readOnArrow{
  transition: transform 0.5s 2s  ;
}

.readOnArrow:hover{
  animation-name: move;
  animation-duration: 2s;
  animation-iteration-count:3;
}

@keyframes move {
  0% {transform: scale(1);}
  50% {transform: scale(1.5);}
  100% {transform: scale(1);}
}

.gap{
  width: 20px;
}